<template>
  <v-app>
    <header-tool-bar
      :name="name"
      :sign-out="signOut"
      :drawer="drawer"
      @toggle-drawer="drawer = !drawer"
    />
    <v-layout>
      <menus ref="menu" v-model:drawer="drawer" @update="menuUpdate"></menus>
      <v-main>
        <breadcrumbs :list="breadcrumbs"></breadcrumbs>
        <div v-bind="containerProps">
          <v-row v-if="$root.subTitle">
            <v-col cols="12">
              <h1>{{ $root.subTitle }}</h1>
            </v-col>
          </v-row>
          <router-view />
        </div>
      </v-main>
    </v-layout>
  </v-app>
</template>

<script setup>
import { ref, onMounted, provide, watch } from "vue";
import { auth } from "@/helpers/auth";
import menus from "@/components/menus";
import { my } from "@/requests";
import { useReCaptcha } from "vue-recaptcha-v3";
import Breadcrumbs from "@/components/breadcrumbs.vue";
import headerToolBar from "@/components/headerToolBar.vue";

const route = useRoute();

const breadcrumbs = ref([]);
const name = ref(null);
const pictureUrl = ref("/images/!logged-user.jpg");
const { signOut, refreshToken, user } = auth();
const menu = ref(null);
const drawer = ref(true);
const menuUpdate = (list) => {
  breadcrumbs.value = list;
};
const { instance, recaptchaLoaded } = useReCaptcha();
onMounted(async () => {
  refreshToken();
  name.value = user().nickname;
  await recaptchaLoaded();
  instance.value.hideBadge();
});

const containerProps = computed(() =>
  route.meta.noContainer
    ? {}
    : { class: 'v-container', style: route.meta.noFluid ? '' : 'max-width:100%' }
);

provide("menu", menu);
</script>
