<template>
  <v-breadcrumbs :items="items" class="pb-0" v-if="items.length > 1">
    <template v-slot:prepend><v-icon>mdi-home</v-icon></template>
    <template v-slot:divider><v-icon>mdi-chevron-right</v-icon></template>
  </v-breadcrumbs>
</template>

<script setup>
import { ref, watch } from 'vue'
import { useRoute } from 'vue-router'
const props = defineProps(['list'])
const items = ref([])
const route = useRoute()
watch(() => props.list, a => items.value = [
  { title: '首頁', to: '/' },
  ...a.length ? a.map(b => ({
    title: b.text,
    to: { name: b.url, params: route.params },
  })) : route.matched.filter(b => b.meta.title).map(b => ({
    title: b.meta.title,
    to: b.name
  }))
])
</script>
